import React, { Component } from 'react';
import '@fortawesome/fontawesome-free/css/all.min.css';
import { Link } from "react-router-dom";

export default class Navigation extends Component {
    render() {
        return ( 
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                <div className='container'>
                    <Link className="navbar-brand" to="/frontend-mern-app/index.html"><i className="far fa-sticky-note"></i> Notes App</Link>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ml-auto">
                            <li className="nav-item">
                                <Link className="nav-link mx-1" to="/frontend-mern-app/index.html">Notes</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link mx-1" to="/frontend-mern-app/new">Create Note</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link mx-1" to="/frontend-mern-app/users/new">Create User</Link>
                            </li>
                        </ul>
                    </div>                    
                </div>
            </nav>
        )
    }
}