import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css'
import './App.css';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import Navigation from "./components/Navigation";
import NotesList from "./components/NotesList";
import NoteForm from "./components/NoteForm";
import UserForm from "./components/UserForm";

function App() {
  return (
    <Router>
      <Navigation/>
      <div className="container p-4">
        <Route path="/frontend-mern-app/index.html" exact component={NotesList}/>
        <Route path="/frontend-mern-app/:id/edit" component={NoteForm}/>
        <Route path="/frontend-mern-app/new" component={NoteForm}/>
        <Route path="/frontend-mern-app/users/new" component={UserForm}/>
      </div>
    </Router>
  );
}

export default App;
