import React, { Component } from 'react'
import axios from 'axios'
import { format } from 'timeago.js'
import { Link } from 'react-router-dom'

export default class NotesList extends Component {

    state = {
        notes: []
    }

    async componentDidMount() {
        this.getNotes();
    }

    async getNotes() {
        const res = await axios.get("https://ignaciomartinelias-mern-app.herokuapp.com/api/notes");
        this.setState({notes: res.data})
    }   

    deleteNote = async (id) => {
         await axios.delete("https://ignaciomartinelias-mern-app.herokuapp.com/api/notes/" + id);
         this.getNotes();
    }

    render() {
        return (
            <div className="row">
                {this.state.notes.map(note => (
                    <div className="col-md-4 p-2" key={note._id}>
                        <div className="card">
                            <div className="card-header d-flex justify-content-between">
                                <h5>{note.title}</h5>
                                <Link className="btn btn-secondary btn-sm ml-2" to={`/frontend-mern-app/${note._id}/edit`}>Edit</Link>
                            </div>
                            <div className="card-body d-flex flex-column">
                                <p>{note.content}</p>
                                <div className="d-flex justify-content-between mt-auto">
                                    <h6>by {note.author}</h6>
                                    <em>{format(note.date)}</em>
                                </div>
                            </div>
                            <div className="card-footer">
                                <button className="btn btn-danger btn-sm" onClick={() => this.deleteNote(note._id)}>Delete</button>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        )
    }
}
