import React, { Component } from 'react';
import axios from 'axios';

export default class UserForm extends Component {

    state = {
        users: [],
        username: ""
    }

    async getUsers () {
        const res = await axios.get("https://ignaciomartinelias-mern-app.herokuapp.com/api/users");
        this.setState({users: res.data})
    }

    deleteUser = async id => {
        console.log(id);
        await axios.delete('https://ignaciomartinelias-mern-app.herokuapp.com/api/users/' + id);
        this.getUsers();
    }

    async componentDidMount(){
        this.getUsers();
    }

    onChangeUsername = (e) => {
        this.setState({username: e.target.value})
    }

    onSubmitForm = async (e) => {
        e.preventDefault();
        await axios.post('https://ignaciomartinelias-mern-app.herokuapp.com/api/users', {username: this.state.username});
        this.getUsers();
        this.setState({username: ""}); 
    }

    render() {
        return (
            <div className="row">
                <div className="col-md-4">
                    <div className="card card-body">
                        <h3>Create New User</h3>
                        <form onSubmit={this.onSubmitForm}>
                            <div className="form-group">
                                <input type="text" className="form-control" placeholder="username" value={this.state.username} onChange={this.onChangeUsername}/>
                            </div>
                            <button type="submit" className="btn btn-dark btn-sm">Add New User</button>
                        </form>
                    </div>
                </div>
                <div className="col-md-8">
                    <ul className="list-group">
                        {
                            this.state.users.map((user) => {
                                return <li 
                                className="list-group-item list-group-item-action" 
                                key={user._id}
                                onDoubleClick={() => this.deleteUser(user._id)}>
                                    {user.username}
                                </li>
                             })
                        }
                    </ul>
                </div>
            </div>
        )
    }
}
