import React, { Component } from 'react'
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'
import axios from 'axios';

export default class NoteForm extends Component {

    state = {
        title: '',
        content: '',
        userSelected: '',
        users: [],
        date: new Date(),
        editing: false
    }

    async componentDidMount() {
        const res = await axios.get("https://ignaciomartinelias-mern-app.herokuapp.com/api/users");
        this.setState({users: res.data.map(user => user.username), userSelected: res.data[0].username});
        if(this.props.match.params.id){
            const res = await axios.get("https://ignaciomartinelias-mern-app.herokuapp.com/api/notes/" + this.props.match.params.id);
            this.setState({editing: true, title: res.data.title, content: res.data.content, date: new Date(res.data.date), userSelected: res.data.author });
        }
    }

    onChange = (e) => {
        this.setState({[e.target.name]: e.target.value})
    }

    onChangeDate = (date) => {
        this.setState({date: date});
    }

    onSubmitForm = async (e) => {
        e.preventDefault();
        if(this.state.editing){
            await axios.put('https://ignaciomartinelias-mern-app.herokuapp.com/api/notes/' + this.props.match.params.id , {title: this.state.title, content: this.state.content, author: this.state.userSelected, date: this.state.date});
        } else {
            await axios.post('https://ignaciomartinelias-mern-app.herokuapp.com/api/notes', {title: this.state.title, content: this.state.content, author: this.state.userSelected, date: this.state.date});
        }
        window.location = "/frontend-mern-app/index.html";
    }

    render() {
        return (
            <div className="col-md-6 offset-md-3">
                <div className="card card-body">
                    <h4>{this.state.editing ? 'Edit Note' : 'Create a Note'}</h4>
                        <form onSubmit={this.onSubmitForm}>
                            <div className="form-group">
                                <select name="userSelected" className="form-control" onChange={this.onChange} value={this.state.userSelected}>
                                    {
                                        this.state.users.map((user) => <option key={user} value={user}>{user}</option>)
                                    }
                                </select>
                            </div>
                            <div className="form-group">
                                <input type="text" name="title" className="form-control" placeholder="Title" value={this.state.title} onChange={this.onChange} required />
                            </div>
                            <div className="form-group">
                                    <textarea className="form-control" name="content" onChange={this.onChange} value={this.state.content} placeholder="Content" required></textarea>    
                            </div>
                            <div className="form-group">
                                <DatePicker className="form-control" selected={this.state.date} onChange={this.onChangeDate}/>
                            </div>
                            <button type="submit" className="btn btn-dark btn-sm">Save Note</button>
                        </form>
                </div>
            </div>
        )
    }
}
